
.App {
  text-align: center;
}
.iconButton:hover{
  background-color: gray !important;
  box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.4);
}
.iconButton{
  transition: all 0.2s !important;
}
.todoCard{
  transition: all 0.2s !important;
}
.todoCard:hover{
  padding-top: 20px;
  padding-bottom: 20px;
  box-shadow: 0px 7px 7px rgba(0, 0, 0, 0.4);
} 
